.t-fail{ background-position: -64px -256px; }

.t-filename
{
    position: relative;
    display: inline-block;
    min-width: 120px;
    max-width: 200px;
    vertical-align: middle;
    zoom:1;
    
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    white-space: nowrap;
}

* html .t-filename
{
    width: 200px;
    display: inline;
}

*+html .t-filename
{
    display: inline;
}

.t-progress,
.t-progress-status
{
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 118px;
    height: 2px;
    border-style: solid;
    border-width: 1px;
    background-repeat: repeat-x;
    
	-moz-border-radius: 2px;
	-webkit-border-radius: 2px;
	border-radius: 2px;
}

.t-progress-status
{
    left: -1px;
    bottom: -1px;
    width: 50%;
}

.t-upload-selected
{
    min-width: 86px;
    box-sizing: content-box;
    -moz-box-sizing: content-box;
    -ms-box-sizing: content-box;
    margin: 3px 0 0;
}

*+html .t-upload-selected { min-width: 100px; }

/* ImageBrowser */

.t-toolbar-wrap .t-dropzone em,
.t-toolbar-wrap .t-upload-files
{
    display: none;
}

.t-toolbar-wrap .t-dropzone
{
    border: 0;
    padding: 0;
    background-color: transparent;
}

.t-search-wrap
{
       float: right;
       width: auto;
       position: relative;
       padding: 0;
}

.t-search-wrap label
{
    position: absolute;
    top: 0;
    left: 4px;
    line-height: 20px;
    font-style: italic;
}

.t-search-wrap input.t-input
{
    width: 158px;
    padding-left: 0;
    padding-right: 0;
}

.t-search
{
    background-position: -64px -176px;
}

.t-search-wrap .t-search
{
    position:absolute;
    top:1px;
    right:2px;
}

.t-breadcrumbs
{
    float: left;
    position: relative;
    width: 560px;
}

.t-breadcrumbs-wrap
{
    position:absolute;
    z-index:1;
    top:0;
    left:0;
    padding-left:5px;
    line-height:18px;
}

html input.t-breadcrumbs-input
{
    width:100%;
    padding-left:2px;
    font-family:inherit;
}

.t-breadcrumbs .t-link,
.t-breadcrumbs-wrap .t-icon
{
    text-decoration: none;
    vertical-align: middle;
    margin-top: 0;
}

.t-breadcrumbs .t-link:hover
{
    text-decoration:underline;
}

.t-imagebrowser .t-breadcrumbs .t-first
{
    text-decoration:none;
    cursor:default;
}

.t-imagebrowser .t-toolbar
{
    clear:both;
    border-style:solid;
    border-width:1px;
    margin: 8px 0 0;
    padding: 2px 0 3px;
    background-repeat:repeat-x;
    line-height:23px;
    white-space:nowrap; /*required by WebKit*/
}

.t-imagebrowser .t-upload
{
    float: left;
}

.t-imagebrowser .t-upload-button
{
    width: auto;
    vertical-align: top;
}

.t-imagebrowser .t-upload .t-icon
{
    vertical-align: bottom;
}

*+html .t-imagebrowser .t-upload-button, *+html .t-imagebrowser .t-upload .t-icon { vertical-align: baseline; position: relative; top: 1px; }
* html .t-imagebrowser .t-upload-button, * html .t-imagebrowser .t-upload .t-icon { vertical-align: baseline; position: relative; top: 1px; }

*+html .t-imagebrowser .t-upload .t-icon { top: 2px; }
* html .t-imagebrowser .t-upload .t-icon { top: 2px; }

*+html .t-imagebrowser .t-toolbar .t-button-icon { vertical-align: middle; }
* html .t-imagebrowser .t-toolbar .t-button-icon { vertical-align: middle; }

.t-addfolder
{
    background-position:-64px -192px;
}

.t-goup
{
    background-position:-64px -208px;
}

.t-toolbar .t-state-disabled
{
    opacity:.5;
    filter:alpha(opacity=50);
}

.t-toolbar-wrap
{
    float:left;
}

.t-tiles-arrange
{
    float:right;
}

* html .t-tiles-arrange
{
    margin: 3px 0 0;
}

.t-tiles-arrange .t-arrow-down
{
    margin:0 2px 0 3px;
}

.t-tiles-arrange a
{
    text-decoration:none;
}

.t-tiles
{
    clear:both;
    height:390px;
    border-style:solid;
    border-width:1px;
    border-top-width:0;
    margin:0 0 1.4em;
    padding:9px;
    overflow:auto;
    line-height:1.2;
}

.t-tile
{
    width:223px;
    height:88px;
    overflow:hidden;
    float:left;
    border-width:1px;
    border-style:solid;
    margin:1px;
    padding:0 0 4px;
    cursor:pointer;

    -moz-border-radius: 4px;
    -webkit-border-radius: 4px;
    border-radius: 4px;
}

.t-imagebrowser .t-thumb
{
    float: left;
    display: inline;
    width: 86px;
    height: 86px;
    margin: 4px 10px 0 4px;
    
	-khtml-user-select: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	user-select: none;
}

.t-imagebrowser .t-image
{
    margin:2px 0 0 2px;

    -moz-box-shadow:1px 1px 3px #aaa;
    -webkit-box-shadow:1px 1px 3px #aaa;
    box-shadow:1px 1px 3px #aaa;
}

.t-imagebrowser .t-folder
{
    width:80px;
    height:80px;
    background-position:0 -200px;
    background-repeat:no-repeat;
}

.t-goback
{
    background-position:-64px -224px;
}

.t-imagebrowser .t-loading
{
    margin:35px 0 0 33px;
}

.t-tile strong,
.t-tile input
{
    margin:10px 0 4px;
    font-weight:normal;
}

.t-tile strong
{
    float: left;
    width: 120px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.t-tile input
{
    width: 100px;
}

.t-tile em,
.t-tile .t-filesize
{
    clear: left;
    font-weight: normal;
    font-style: normal;
}

.t-tile strong,
.t-tile input,
.t-tile em,
.t-tile .t-filesize
{
    display:block;
}

.t-imagebrowser .t-form-text-row input
{
width:628px;
}

/*
* html .t-imagebrowser .t-form-text-row input{width:610px;}
*+html .t-imagebrowser .t-form-text-row input{width:610px;}
*/

.t-tile-empty
{
    margin:160px 0 0;
}

.t-tile-empty .t-dialog-upload
{
    font-weight:bold;
    font-size:120%;
}

.t-tile-empty strong
{
    display:block;
    margin:0 0 0.2em;
    font-size:3em;
    font-weight:normal;
}

.t-tile-empty,
.t-tile-empty .t-button-wrapper
{
    text-align:center;
}

.t-touch-scrollbar
{
    position:absolute;
    display:none;
    z-index:200000;
    height: 8px; 
    width: 8px; 
    border: 1px solid #8a8a8a;
    background-color: #858585; 
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px; 
    border-radius: 4px;
}
        
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
    .t-touch-scrollbar
    {
        height: 12px; 
        width: 12px; 
        -webkit-border-radius: 7px; 
    }
}

/* Chart */

div.t-chart
{
    border: none;
    background-color: transparent;
}
